import React, { useContext } from "react";
import PropTypes from 'prop-types';
import VKBridge from '@vkontakte/vk-bridge'
import { Button, RichCell, CardGrid, PanelHeaderContent, SimpleCell, Card, Cell, Group, List, Avatar, Panel, PanelHeader, PanelHeaderBack, platform, IOS, Epic, Tabbar, TabbarItem } from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28FavoriteOutline from '@vkontakte/icons/dist/28/favorite_outline';
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon20NewsfeedOutline from '@vkontakte/icons/dist/20/newsfeed_outline';
import { Icon28MoonOutline } from "@vkontakte/icons";

import { SchemeContext } from "./hooks/hooks";

const orangeBackground = {
  backgroundImage: 'linear-gradient(135deg, #ffb73d, #ffa000)'
};

const grayBackground = {
  backgroundImage: 'linear-gradient(#b1b6bd)'
};

const blueBackground = {
  backgroundColor: 'var(--accent)'
};

const moneyBackground = {
  backgroundImage: 'https://sun9-26.userapi.com/c206824/v206824078/122e90/MEDeH1OFL6k.jpg'
}

function tog() {
  useContext(SchemeContext);
}

const Profile = ({ props, fetchedUser, id, go }) => (
	<Panel id={id}>
       		<PanelHeader>
       			Профиль
            </PanelHeader>
				<CardGrid>
                    <Card size="l">
                      <SimpleCell before={
                          <Icon28MoonOutline />
                          }
                            onClick={tog()}
                            size="m"
                            multiline
                            description="*оттенок приложения"
                          >
                            Сменить тему
                        </SimpleCell>
                    </Card>
                    <Card size="l">
                        <SimpleCell before={
                            <Icon20NewsfeedOutline width={28} height={28} />
                        }
                                    onClick={() => VKBridge.send("VKWebAppShowWallPostBox", {"message": "Я уже использую приложение Тупые саши, как же я вас блин ненавижу, а ты? Присоединяйся: https://vk.cc/bwrZV7"})}
                                    size="m"
                                    multiline
                                    description="Опубликовать запись с поддержкой у себя на стене"

                        >
                            Помочь приложению
                        </SimpleCell>
                    </Card>
             	    <Card size="l">
                        <RichCell multiline
                            disabled
                            before={
                            <Avatar mode="image"
                            id="persik"
                            size={64}
                            >
                            <persik height={64} width={64}/>
                            </Avatar>
                                  }
                                  size="l"
                                  text="Перейти в сообщество"
                                  actions={
                                      <Button mode="secondary"
                                              target="_blank"
                                              href="https://vk.com/s_ashaapp"
                                              rel="noreferrer"
                                      >
                                          Открыть
                                      </Button>
                                  }
                        >
                            тупые саши как же я вас блин ненавижу
                        </RichCell>
                    </Card>
                    <Card size="l">
                        <SimpleCell before={
                            <Icon28AddOutline/>
                        }
                                    onClick={() => VKBridge.send("VKWebAppAddToCommunity", {})}
                                    size="m"
                                    multiline
                                    description="Установите приложение в свое сообщество за один клик"

                        >
                            Установить приложение
                        </SimpleCell>
                    </Card>
                </CardGrid>

  <Epic tabbar={
        <Tabbar>
          <TabbarItem
            onClick={go}
            data-to="home"
            text="Главная"
          ><Avatar size={28} style={blueBackground}><Icon28HomeOutline width={14} height={14} fill="#fff" /></Avatar></TabbarItem>
          <TabbarItem
            onClick={go}
            data-to="persik"
            text="Помочь автору"
          ><Avatar size={28} style={orangeBackground}><Icon24Favorite width={14} height={14} fill="#fff" /></Avatar></TabbarItem>
        <TabbarItem
            onClick={go}
            data-to="profile"
            text="Профиль"
          ><Avatar size={28} style={grayBackground}><Icon28User width={14} height={14} fill="#000000" /></Avatar></TabbarItem>
        </Tabbar>
      }>
    </Epic>
	</Panel>
);

Profile.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
  fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),
};

export default Profile;
